import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';

import { BattleCalculatorPage } from './battle-calculator.page';
import { SharedModule } from '../shared/shared.module';
import { OneOnOneComponent } from './one-on-one/one-one-one.component';
import { RouterModule } from '@angular/router';
import { BattleComponent } from './battle/battle.component';
import { BrModalComponent } from './br-modal/br-modal.component';
import { CodeComponent } from './code/code.component';

@NgModule({
  imports: [
    CommonModule,
    IonicModule,
    ReactiveFormsModule,
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: BattleCalculatorPage
      }
    ]),
  ],
  entryComponents: [BrModalComponent],
  declarations: [BattleCalculatorPage, OneOnOneComponent, BattleComponent, BrModalComponent, CodeComponent]
})
export class BattleCalculatorPageModule {}
